.contact {
    background: linear-gradient(to bottom, var(--colour-background-tertiary), var(--colour-background-primary));
    text-align: center;
    padding-top: 40px;
}
.contact .content .links {
    display: flex;
    justify-content: center;
}
.contact .content a {
    text-align: center;
    margin: 20px;
    text-decoration: none;
    border-radius: 10px;
}
.contact .content p {
    margin: 0;
    font-size: 1.2em;
    color: black;
}
.contact .content a img {
    width: 60px;
}